'use strict';

var createNode = require('./generator').createNode;

var skip = {
  name: 'skip',
  fn: function(c) {}
};

var reverse = {
  name: 'reverse',
  fn: function(c) { c.reverse(); }
};

var insertFirst = {
  name: 'insertFirst',
  fn: function (c) { c.unshift(createNode(c.length)); }
};

var insertLast = {
  name: 'insertLast',
  fn: function (c) { c.push(createNode(c.length)); }
};

var removeFirst = {
  name: 'removeFirst',
  fn: function (c) { c.shift(); }
};

var removeLast = {
  name: 'removeLast',
  fn: function (c) { c.pop(); }
};

var moveFromEndToStart = {
  name: 'moveFromEndToStart',
  fn: function (c) { c.unshift(c.pop()); }
};

var moveFromStartToEnd = {
  name: 'moveFromStartToEnd',
  fn: function (c) { c.push(c.shift()); }
};

var shuffle = {
  name: 'shuffle',
  fn: function(c) {
    var i = c.length;
    var r;
    var tmp;

    while (i !== 0) {
      r = Math.floor(Math.random() * (i--));

      tmp = c[i];
      c[i] = c[r];
      c[r] = tmp;
    }
  }
};

module.exports = {
  skip: skip,
  reverse: reverse,
  insertFirst: insertFirst,
  insertLast: insertLast,
  removeFirst: removeFirst,
  removeLast: removeLast,
  moveFromEndToStart: moveFromEndToStart,
  moveFromStartToEnd: moveFromStartToEnd,
  shuffle: shuffle
};
